import React, { useState, useRef, ReactNode } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { adminLogout } from '../api';
import { message, Breadcrumb } from 'antd';
import styles from './AdminLayout.module.css';
import NotificationBell, { NotificationBellRef } from '../components/NotificationBell';
import NotificationModal from '../components/NotificationModal';

export interface MenuItem {
  key: string;
  label: string;
  icon?: string;
  path: string;
  children?: MenuItem[];
}

export interface AdminLayoutProps {
  children: ReactNode;
  menuItems?: MenuItem[];
}

const defaultMenuItems: MenuItem[] = [
  {
    key: 'dashboard',
    label: '',
    icon: '',
    path: '/admin',
  },
  {
    key: 'users',
    label: '',
    icon: '',
    path: '/admin/users',
  },
  {
    key: 'tasks',
    label: '',
    icon: '',
    path: '/admin/tasks',
  },
  {
    key: 'customer-service',
    label: '',
    icon: '',
    path: '/admin/customer-service',
  },
  {
    key: 'job-positions',
    label: '',
    icon: '',
    path: '/admin/job-positions',
  },
  {
    key: 'experts',
    label: '',
    icon: '',
    path: '/admin/experts',
  },
  {
    key: 'coupons',
    label: '',
    icon: '',
    path: '/admin/coupons',
  },
  {
    key: 'disputes',
    label: '',
    icon: '',
    path: '/admin/disputes',
  },
  {
    key: 'refunds',
    label: '',
    icon: '',
    path: '/admin/refunds',
  },
  {
    key: 'notifications',
    label: '',
    icon: '',
    path: '/admin/notifications',
  },
  {
    key: 'invitations',
    label: '',
    icon: '',
    path: '/admin/invitations',
  },
  {
    key: 'forum',
    label: '',
    icon: '',
    path: '/admin/forum',
  },
  {
    key: 'flea-market',
    label: '',
    icon: '',
    path: '/admin/flea-market',
  },
  {
    key: 'leaderboard',
    label: '',
    icon: '',
    path: '/admin/leaderboard',
  },
  {
    key: 'banners',
    label: 'Banner',
    icon: '',
    path: '/admin/banners',
  },
  {
    key: 'reports',
    label: '',
    icon: '',
    path: '/admin/reports',
  },
];

const PATH_LABELS: Record<string, string> = {
  '/admin': '',
  '/admin/users': '',
  '/admin/experts': '',
  '/admin/tasks': '',
  '/admin/job-positions': '',
  '/admin/customer-service': '',
  '/admin/coupons': '',
  '/admin/disputes': '',
  '/admin/refunds': '',
  '/admin/notifications': '',
  '/admin/invitations': '',
  '/admin/forum': '',
  '/admin/flea-market': '',
  '/admin/leaderboard': '',
  '/admin/banners': 'Banner',
  '/admin/reports': '',
  '/admin/settings': '',
};

export const AdminLayout: React.FC<AdminLayoutProps> = ({
  children,
  menuItems = defaultMenuItems,
}) => {
  const navigate = useNavigate();
  const location = useLocation();
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [showUserMenu, setShowUserMenu] = useState(false);
  const [showNotificationModal, setShowNotificationModal] = useState(false);
  const notificationBellRef = useRef<NotificationBellRef>(null);

  const breadcrumbItems = React.useMemo(() => {
    const pathname = location.pathname;
    const items: { title: string }[] = [{ title: 'LinkU ' }];
    if (PATH_LABELS[pathname] && pathname !== '/admin') {
      items.push({ title: PATH_LABELS[pathname] });
    }
    return items;
  }, [location.pathname]);

  const handleLogout = async () => {
    try {
      await adminLogout();
      message.success('');
      navigate('/login');
    } catch (error) {
      message.error('');
      console.error(error);
    }
  };

  const isActiveRoute = (path: string) => {
    if (path === '/admin') {
      return location.pathname === '/admin';
    }
    return location.pathname.startsWith(path);
  };

  const handleMenuClick = (item: MenuItem) => {
    navigate(item.path);
  };

  return (
    <div className={styles.layout}>
      {/* Sidebar */}
      <aside className={`${styles.sidebar} ${sidebarCollapsed ? styles.collapsed : ''}`}>
        <div className={styles.sidebarHeader}>
          <h1 className={styles.logo}>
            {sidebarCollapsed ? 'L' : 'LinkU '}
          </h1>
          <button
            className={styles.toggleButton}
            onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
            aria-label={sidebarCollapsed ? '' : ''}
          >
            {sidebarCollapsed ? '' : ''}
          </button>
        </div>

        <nav className={styles.nav}>
          {menuItems.map((item) => (
            <button
              key={item.key}
              className={`${styles.navItem} ${isActiveRoute(item.path) ? styles.active : ''}`}
              onClick={() => handleMenuClick(item)}
              title={sidebarCollapsed ? item.label : undefined}
            >
              {item.icon && <span className={styles.icon}>{item.icon}</span>}
              {!sidebarCollapsed && <span className={styles.label}>{item.label}</span>}
            </button>
          ))}
        </nav>
      </aside>

      {/* Main Content Area */}
      <div className={styles.mainContainer}>
        {/* Top Bar */}
        <header className={styles.topBar}>
          <Breadcrumb items={breadcrumbItems} className={styles.breadcrumb} />

          <div className={styles.topBarActions}>
            <NotificationBell
              ref={notificationBellRef}
              userType="admin"
              onOpenModal={() => setShowNotificationModal(true)}
            />
            {/* User Menu */}
            <div className={styles.userMenuContainer}>
              <button
                className={styles.userButton}
                onClick={() => setShowUserMenu(!showUserMenu)}
              >
                <span className={styles.userAvatar}></span>
                <span className={styles.userName}></span>
              </button>

              {showUserMenu && (
                <>
                  <div
                    className={styles.userMenuOverlay}
                    onClick={() => setShowUserMenu(false)}
                  />
                  <div className={styles.userMenu}>
                    <button
                      className={styles.userMenuItem}
                      onClick={() => {
                        navigate('/admin/settings');
                        setShowUserMenu(false);
                      }}
                    >
                       
                    </button>
                    <button
                      className={styles.userMenuItem}
                      onClick={() => {
                        handleLogout();
                        setShowUserMenu(false);
                      }}
                    >
                       
                    </button>
                  </div>
                </>
              )}
            </div>
          </div>
        </header>

        {/* Page Content */}
        <main className={styles.content}>{children}</main>

        {/* Footer */}
        <footer className={styles.footer}>
          <p> 2025 LinkU. All rights reserved.</p>
        </footer>
      </div>

      <NotificationModal
        isOpen={showNotificationModal}
        onClose={() => setShowNotificationModal(false)}
        userType="admin"
        onNotificationRead={() => notificationBellRef.current?.refreshUnreadCount()}
      />
    </div>
  );
};

export default AdminLayout;
